<template>
    <div class="pc-body container pc-server">
        <div class="panel panel-success">
            <div class="panel-heading">
                server
            </div>
            <div class="panel-body">
                <yz-tree-table :data="data">
                    <yz-tree-column prop="name"></yz-tree-column>
                    <yz-tree-column>
                        <template scope="scope">
                            <button>{{scope.row}}</button>
                        </template>
                    </yz-tree-column>
                </yz-tree-table>
    
            </div>
        </div>
    </div>
</template>
<script>
import { yzTreeTable, yzTreeColumn } from './treeTable'

export default {
    name: "serverBody",
    data() {
        return {
            data: [{
                name: "gzh1",
                age: 11,
                children: [{
                    name: "gzh1-1",
                    age: 111
                }, {
                    name: "gzh1-2",
                    age: 112
                }]
            }, {
                name: "gzh2",
                age: 11
            }, {
                name: "gzh3",
                age: 11,
                children: [{
                    name: "gzh3-1",
                    age: 111
                }, {
                    name: "gzh3-2",
                    age: 112,
                    children: [{
                        name: "gzh3-2-1",
                        age: 111,
                        children: [{
                            name: "gzh3-2-1-1",
                            age: 111
                        }]
                    }

                    ]
                }]
            }, {
                name: "gzh4",
                age: 11
            }]
        }
    },
    components: { yzTreeTable, yzTreeColumn },
    created: function () {
        this.getData();
    },
    methods: {
        getData: function () {
            var obj={

            };
            $.ajax({
                url: "http://localhost:10000/monitor/queryCrossServices.do",
                type: "post",
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                },
                data: JSON.stringify(obj),
                dataType: "json",
                // 获取服务器返回的json结果
                success: function (resp) {
                    console.log(resp)
                },
                error: function () {
                    console.log('更新用户服务请求失败！')
                }
            });
            $.ajax({


            })
        }
    }
}
</script>
 <style lang="less" src="../less/server.less"></style>



