<template>
    <div class="yz-treetable">
        <table class="table table-border">
            <thead>
                <tr>
                    <th>
                        序号
                    </th>
                    <th>
                        名称
                    </th>
                    <th>
                        年龄
                    </th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(rowinfo,$index) in dealData">
                    <td>
                        <div class="table-tree-icon" :style="iconStyle(rowinfo)" @click="toggleChildren(rowinfo,$index)">
                            <i class="yz-icon" :class="iconClass(rowinfo)"></i>
                        </div>
                    </td>
                    <td>{{rowinfo.name}}</td>
                    <td>{{rowinfo.age}}</td>
                </tr>
            </tbody>
        </table>
        <!--<slot></slot>-->
    </div>
</template>
<script>
export default {
    name: "treeTable",
    componentName: "treeTable",
    props: {
        data: Array
    },
    data() {
        return {
            dealData: this.data.concat()
        }
    },
    computed:{
        
    },
    created:function(){
        this.setParent(this.data,1,-1)
    },
    methods: {
        
        setParent: function (data, level,parentIndex) {
            console.log(data,level,parentIndex)
            var ilength = data.length;
            for (var i = 0; i < ilength; i++) {
                var currentData = data[i];
                currentData.currentLevel = level;
                // currentData.parentLevel = 0;
                if(parentIndex == -1){
                    currentData.parentIndex = null;
                }else{
                    currentData.parentIndex = parentIndex;
                }
                console.log(currentData.children);

                if (currentData.children && currentData.children.length != 0) {
                    var currentIndex = parentIndex === -1 ? i:currentData.parentIndex +"-"+ i;
                    this.setParent(currentData.children, level+1,currentIndex);
                }
            }

        },
        iconStyle:function(rowinfo){
            return {
                marginLeft:`${rowinfo.currentLevel*10}px`
            }
        },
        iconClass:function(rowinfo){
            if(!rowinfo.children || rowinfo.children.length ==0){
                return {
                    'icon-default':true
                }
            }else if(rowinfo.isOpen){
                return {
                    'icon-open':true
                }
            }else{
                return {
                    'icon-close':true
                }
            }

        },
        toggleChildren:function(rowinfo,$index){
            console.log('toggle',rowinfo,$index);
            if(!rowinfo.children ||rowinfo.children.length ==0 ){
                return;
            }
            if(rowinfo.isOpen){
                this.closeRow(rowinfo,$index);
            }else{
                 this.openRow(rowinfo,$index);
            }
            
        },

        openRow:function(rowinfo,$index){
            if(rowinfo.isOpen)  return;
            for(var i=0;i<rowinfo.children.length;i++){
                this.dealData.splice($index+i+1,0,rowinfo.children[i])
            }
            rowinfo.isOpen = true;
        },
        closeRow:function(rowinfo,$index){
            if(!rowinfo.isOpen)  return;
            var currentLevel =rowinfo.currentLevel,spliceLeng = 0;
            for(var i=$index+1,ilength=this.dealData.length;i<ilength;i++){
                var currentData = this.dealData[i];
                currentData.isOpen =false;
                console.log(currentData.currentLevel >= currentLevel)
                if(currentData.currentLevel <= currentLevel){
                    spliceLeng = i-$index-1;
                    break;
                }
            }
            console.log(spliceLeng);
            this.dealData.splice($index+1,spliceLeng);
             rowinfo.isOpen = false;
        }
    }
}
</script>


